// data/places.ts

export type PlaceCategory = "coffee" | "restaurant" | "bar";

export type PlaceTag =
  | "laptop-friendly"
  | "first-date"
  | "cheap"
  | "fancy"
  | "quiet"
  | "groups"
  | "solo";

export type Place = {
  id: string;
  name: string;
  neighborhood: string;
  category: PlaceCategory;
  tags: PlaceTag[];
  rating: number; // your rating, 1–5
  shortBlurb: string;
  googleMapsUrl?: string;
};

